    changedForm = function() {
      return !angular.equals(vm.model, vm.originalModel);
    };
    locationChangeStartUnbind = $scope.$on('$stateChangeStart', function(event) {
      var answer;
      if (changedForm()) {
        answer = confirm($translate.instant('mnoe_admin_panel.dashboard.settings.modal.confirm.unsaved'));
        if (!answer) {
          return event.preventDefault();
        }
      }
    });
    $window.onbeforeunload = function(e) {
      if (changedForm()) {
        return true;
      } else {
        return void 0;
      }
    };
    $scope.$on('$destroy', function() {
      $window.onbeforeunload = void 0;
      return locationChangeStartUnbind();
    });
