(function() {
  this.App.controller('UpdateStaffClientsController', ["staff", "$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeUsers", "MnoErrorsHandler", "MnoeOrganizations", function(staff, $filter, $stateParams, $log, $uibModalInstance, toastr, MnoeUsers, MnoErrorsHandler, MnoeOrganizations) {
    'ngInject';
    var displayCurrentState, fetchOrganizations, setAllOrganizationsList, setSearchOrganizationsList, syncOrganizationsWithChanges, vm;
    vm = this;
    vm.changes = {
      add: [],
      remove: []
    };
    vm.organizations = {
      search: '',
      nbItems: 10,
      page: 1,
      pageChangedCb: function(nbItems, page) {
        var offset;
        vm.organizations.nbItems = nbItems;
        vm.organizations.page = page;
        offset = (page - 1) * nbItems;
        return fetchOrganizations(nbItems, offset);
      }
    };
    fetchOrganizations = function(limit, offset, sort) {
      var params;
      if (sort == null) {
        sort = 'name';
      }
      vm.organizations.loading = true;
      params = {
        account_manager_id: staff.id
      };
      return MnoeOrganizations.list(limit, offset, sort, params).then(function(response) {
        vm.organizations.totalItems = response.headers('x-total-count');
        return vm.organizations.list = syncOrganizationsWithChanges(response.data);
      }).then(function() {
        return vm.organizations.loading = false;
      });
    };
    displayCurrentState = function() {
      setAllOrganizationsList();
      return fetchOrganizations(vm.organizations.nbItems, 0);
    };
    setAllOrganizationsList = function() {
      return vm.organizations.widgetTitle = 'All organisations';
    };
    vm.searchChange = function() {
      if (vm.organizations.search.length >= 3) {
        vm.searchMode = true;
        return setSearchOrganizationsList(vm.organizations.search);
      } else if (vm.searchMode) {
        vm.searchMode = false;
        return displayCurrentState();
      }
    };
    setSearchOrganizationsList = function(search) {
      var params, terms;
      vm.organizations.loading = true;
      vm.organizations.widgetTitle = 'Search result';
      search = vm.organizations.search.toLowerCase();
      terms = {
        'name.like': "%" + search + "%"
      };
      params = {
        account_manager_id: staff.id
      };
      return MnoeOrganizations.search(terms, params).then(function(response) {
        vm.organizations.totalItems = response.headers('x-total-count');
        return vm.organizations.list = syncOrganizationsWithChanges($filter('orderBy')(response.data, 'name'));
      })["finally"](function() {
        return vm.organizations.loading = false;
      });
    };
    displayCurrentState();
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeUsers.updateStaffClients(staff, vm.changes).then(function() {
        $uibModalInstance.close();
        return toastr.success("mnoe_admin_panel.dashboard.staff.update_staff.toastr_success", {
          extraData: {
            staff_name: staff.name + " " + staff.surname
          }
        });
      }, function(error) {
        toastr.error('mnoe_admin_panel.dashboard.staff.update_staff.modal.toastr_error', {
          extraData: {
            staff_name: staff.name + " " + staff.surname
          }
        });
        return $log.error("An error occurred while updating staff:", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.isDisabled = function() {
      return vm.isLoading || (vm.changes.add.length === 0 && vm.changes.remove.length === 0);
    };
    vm.checkBoxChanged = function(organization) {
      var index;
      if (organization.belong_to_account_manager) {
        index = _.indexOf(vm.changes.remove, organization.id);
        if (index === -1) {
          return vm.changes.add.push(organization.id);
        } else {
          return vm.changes.remove.splice(index, 1);
        }
      } else {
        index = _.indexOf(vm.changes.add, organization.id);
        if (index === -1) {
          return vm.changes.remove.push(organization.id);
        } else {
          return vm.changes.add.splice(index, 1);
        }
      }
    };
    syncOrganizationsWithChanges = function(organizations) {
      return _.map(organizations, function(organization) {
        if (_.contains(vm.changes.add, organization.id)) {
          organization.belong_to_account_manager = true;
        } else if (_.contains(vm.changes.remove, organization.id)) {
          organization.belong_to_account_manager = false;
        }
        return organization;
      });
    };
  }]);

}).call(this);

