(function() {
  this.App.controller('UpdateAccountManagersController', ["$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeSubTenants", "MnoeUsers", "MnoErrorsHandler", "MnoeAdminConfig", "subTenant", function($filter, $stateParams, $log, $uibModalInstance, toastr, MnoeSubTenants, MnoeUsers, MnoErrorsHandler, MnoeAdminConfig, subTenant) {
    'ngInject';
    var fetchStaffs, syncUsersWithChanges, updateSearch, updateSort, vm;
    vm = this;
    vm.changes = {
      add: [],
      remove: []
    };
    vm.staff_roles = MnoeAdminConfig.adminRoles();
    vm.getAdminRoleLabel = function(admin_role) {
      return _.find(vm.staff_roles, function(role) {
        return role.value === admin_role;
      }).label;
    };
    vm.callServer = function(tableState) {
      var search, sort;
      sort = updateSort(tableState.sort);
      search = updateSearch(tableState.search);
      return fetchStaffs(vm.staff.nbItems, vm.staff.offset, sort, search);
    };
    updateSort = function(sortState) {
      var sort;
      if (sortState == null) {
        sortState = {};
      }
      sort = "surname";
      if (sortState.predicate) {
        sort = sortState.predicate;
        if (sortState.reverse) {
          sort += ".desc";
        } else {
          sort += ".asc";
        }
      }
      vm.staff.sort = sort;
      return sort;
    };
    updateSearch = function(searchingState) {
      var attr, ref, search, value;
      if (searchingState == null) {
        searchingState = {};
      }
      search = {};
      if (searchingState.predicateObject) {
        ref = searchingState.predicateObject;
        for (attr in ref) {
          value = ref[attr];
          if (attr === "admin_role") {
            search['where[admin_role]'] = value;
          } else {
            search['where[' + attr + '.like]'] = value + '%';
          }
        }
      }
      vm.staff.search = search;
      return search;
    };
    vm.staff = {
      search: {},
      sort: "surname",
      nbItems: 10,
      page: 1,
      pageChangedCb: function(nbItems, page) {
        var offset;
        vm.staff.nbItems = nbItems;
        vm.staff.page = page;
        offset = (page - 1) * nbItems;
        return fetchStaffs(nbItems, offset);
      }
    };
    fetchStaffs = function(limit, offset, sort, search) {
      if (sort == null) {
        sort = vm.staff.sort;
      }
      if (search == null) {
        search = vm.staff.search;
      }
      vm.staff.loading = true;
      return MnoeUsers.staffs(limit, offset, sort, search).then(function(response) {
        vm.staff.totalItems = response.headers('x-total-count');
        _.each(response.data, function(u) {
          return u.belong_to_sub_tenant = u.sub_tenant_id === subTenant.id;
        });
        return vm.listOfStaff = syncUsersWithChanges(response.data);
      })["finally"](function() {
        return vm.staff.loading = false;
      });
    };
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeSubTenants.updateAccountManagers(subTenant, vm.changes).then(function() {
        $uibModalInstance.close();
        return toastr.success("mnoe_admin_panel.dashboard.sub_tenant.select_account_managers.modal.toastr_success", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
      }, function(error) {
        toastr.error("mnoe_admin_panel.dashboard.sub_tenant.select_account_managers.modal.toastr_error", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
        return $log.error("An error occurred while updating account managers of " + subTenant.name + ".", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
    vm.isDisabled = function() {
      return vm.isLoading || (vm.changes.add.length === 0 && vm.changes.remove.length === 0);
    };
    vm.checkBoxChanged = function(user) {
      var index;
      if (user.belong_to_sub_tenant) {
        index = _.indexOf(vm.changes.remove, user.id);
        if (index === -1) {
          return vm.changes.add.push(user.id);
        } else {
          return vm.changes.remove.splice(index, 1);
        }
      } else {
        index = _.indexOf(vm.changes.add, user.id);
        if (index === -1) {
          return vm.changes.remove.push(user.id);
        } else {
          return vm.changes.add.splice(index, 1);
        }
      }
    };
    syncUsersWithChanges = function(users) {
      return _.map(users, function(user) {
        if (_.contains(vm.changes.add, user.id)) {
          user.belong_to_sub_tenant = true;
        } else if (_.contains(vm.changes.remove, user.id)) {
          user.belong_to_sub_tenant = false;
        }
        return user;
      });
    };
  }]);

}).call(this);

